/* ============ Calendar style start ========== */
.popup_calendar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 104, 162, 0.24);
  z-index: 2;
  opacity: 0;
  visibility: hidden;
  transition: 0.2s all linear;
  max-width: inherit;
}
.popup_calendar:target {
  opacity: 1;
  visibility: visible;
}
.popup_calendar .popup_body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.popup_calendar .popup_content {
  background: #fff;
  padding: 21px 31px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}
.popup_calendar .calendar_items {
  display: flex;
  justify-content: space-between;
  gap: 60px;
}
.popup_calendar .calendar_items a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  border-radius: 15px;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  -ms-border-radius: 15px;
  -o-border-radius: 15px;
  text-decoration: none;
  color: #000000;
  border: 1px solid #CED3DE;
}
.popup_calendar .calendar_title {
  text-align: center;
  font-weight: 400;
  font-size: 20px;
  margin: 0;
}
.popup_calendar .calendar_title span {
  font-weight: 400;
  font-size: 12px;
  color: #8F9BB3;
}
.popup_calendar table tr {
  height: 35px;
}
.popup_calendar table td {
  width: 42px;
  text-align: center;
}
.popup_calendar .days {
  font-weight: 400;
  font-size: 13px;
  color: #8F9BB3;
}
.popup_calendar .out {
  font-weight: 400;
  font-size: 15px;
  color: #8F9BB3;
}
.popup_calendar tbody td {
  font-weight: 400;
  font-size: 15px;
}
.popup_calendar table {
  margin-bottom: 20px;
}
.popup_calendar .btn_done {
  width: 90px;
  padding: 8px 28.5px;
  text-decoration: none;
  background: #09A4FA;
  border-radius: 8px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  -ms-border-radius: 8px;
  -o-border-radius: 8px;
  color: #fff;
}

/* ============ Calendar style end ========== */
